import interfaceurl from '@/utils/interface.js'
import timeAxis from '@/components/time-axis.vue'
import beforeCart from '@/components/before-cart.vue'
//引入store的内容
import {
	mapState,
	mapMutations,
	mapActions
} from 'vuex'
export default {
	computed: mapState(['userInfo']),
	components: {
		timeAxis,
		beforeCart
	},
	data() {
		return {
			commonList: [], //评论列表
			commonParams: {
				productId: 205,
				start: 0,
				length: 10,
				userId: 97
			}, //评论参数
			detail: {}, //宝贝详情
			/*弹窗相关参数*/
			showpopup: false, //提示用户绑定设备
			showdevicepopup: false, //显示设备列表
			showattributepopup: false, //显示设备属性
			type: 'center',
			attrList: [], //设备商品的属性
			devices: [], //用户绑定的设备
			productId: 0, //用户选中的商品ID
			deviceId: 0, //用户选中的设备
		}
	},
	onLoad(options) {
		this.commonParams.userId = this.userInfo.userId;
		let that = this;
		interfaceurl.checkAuth(interfaceurl.detail, {id: options.id}, false).then((res) => {
			that.detail = res.data;
			that.detail.historyVersion = JSON.parse(that.detail.historyVersion)
		});
		this.getComment();
	},
	onShareAppMessage(res) {
	  return {
		title: '商品分享',
		path: '/pages/shop/goodsdetail?id=' + this.detail.id
	  }
	},
	methods: {
		turn() {
			let selectCartList = [];
			this.detail.productId = this.detail.id;
			this.detail.quantity = 1;
			selectCartList.push(this.detail);
			this.$store.commit("setCartOrder", selectCartList);
			this.$turnPage('../shop/orderpay', 'navigateTo');
		},
		addFavor(id) {
			let params = {
				userId: this.userInfo.userId,
				productId: this.detail.id
			}
			interfaceurl.checkAuth(interfaceurl.addCollection, params).then((res) => {
				uni.showToast({
				    title: '收藏成功',
				    icon: 'none',
				    duration: 2000
				});
			});
		},
		getComment() {
			let that = this;
			interfaceurl.checkAuth(interfaceurl.comment, that.commonParams, false).then((res) => {
				that.commonList = res.data;
				for(var item of that.commonList) {
					item.createDt = new Date(item.createDt).format("yyyy-MM-dd hh:mm:ss");
				}
			});
		},
		//弹窗相关方法
		//加入购物车前判断用户是否绑定设备
		beforeSaveCart(productId) {
			//调用子组件的方法
			this.$refs.cart.beforeSaveCart(productId);
		}
	}
}
